import Vue from 'vue'
import Router from 'vue-router'
import TData from './components/Tabledata'
import TMatch from './components/Tablematch'
import MSearch from './components/Search'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {path:'/data',name:'data',component:TData},
    {path:'/match',name:'match',component:TMatch},
    {path:'/search',name:'search',component:MSearch}
  ]
})
