<template>
    <div class="searchdata">
        <h3 v-if="loading">搜索到{{datalist.length}}位"{{$route.query.keyword}}"相关的玩家</h3>
        <h3 v-if="!loading">正在加载...</h3>
        <ul class="playerlist">
            <li class="player" v-for="(item,i) in datalist.slice((currentPage-1)*pagesize,currentPage*pagesize)" :key="i">
                <a @click="tomatch(item)"><img class="userimg" :src="`https://oss.5ewin.com/${item.img}`"><span>{{item.name}}</span></a>
            </li>
        </ul>
        <div class="block">
            <el-pagination
                v-if="datalist.length>=24"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="pagesize"
                layout="total, prev, pager, next, jumper"
                :total="datalist.length">
            </el-pagination>
        </div>
    </div>
</template>

<script>
var qs = require('qs');
export default {
  data() {
      return {
          datalist: [],
          demolist: [],
          currentPage:1,
          pagesize:24,
          loading: true
      }
  },
  methods: {
    handleCurrentChange: function(currentPage){
        this.currentPage = currentPage;
    },
    tomatch(item){
        let postid = {id: item.id};
        this.$axios.post("/data/match/",qs.stringify(postid),{
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            }
        }).then(response=>{
            sessionStorage.setItem("demodict", JSON.stringify(response.data));
            this.$router.push({name:'match',query:{user:item.name}})
        })
    }
  },
  created(){
    //获取数据
    let keyword = this.$route.query.keyword
    let sdata = {search: keyword}
    this.loading = false
    this.$axios.post("/data/search/",qs.stringify(sdata),{
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    }).then(response=>{
        this.datalist = response.data['0']
        this.loading = true
    })
  }
}
</script>

<style scoped>
h3{
    color: white;
}
.playerlist{
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap:wrap;
    justify-content: flex-start;
}
.player{
    margin: 20px 0 0 15px;
    list-style: none;
    border-radius: 3px;
    border: 2px solid white;
}
.player:hover{
    background-color: palevioletred
}
.player a{
    display: flex;
    justify-content: flex-start;
    text-decoration:none;
    color: white;
    width: 230px;
    height: 50px;
}
.player>a>span{
    display: inline-block;
    height: 30px;
    margin-top:16px

}
.userimg{
    width: 46px;
    height: 46px;
    margin: 2px;
}
.block{
    text-align: center;
    margin-top: 30px;
}

</style>