<template>
    <div class="tablematch">
        <table>
            <tr id="tableheader">
                <th class="width50"><span>赛季</span></th>
                <th class="width85"><span>比赛类型</span></th>
                <th class="width250"><span>比赛时间</span></th>
                <th class="width85"><span>比赛耗时</span></th>
                <th class="width110"><span>地图</span></th>
                <th class="width70"><span>比分</span></th>
                <th class="width50"><span>杀敌</span></th>
                <th class="width50"><span>赛果</span></th>
                <th class="width70"><span>贡献值<br>RWS</span></th>
                <th class="width70"><span>技术得分<br>Rating</span></th>
                <th class="width110"></th>
            </tr>
            <tbody v-for="(item,i) in demolist.slice((currentPage-1)*pagesize,currentPage*pagesize)" :key="i">
                <tr class="table">
                    <th class="width50"><span>S{{item.season}}</span></th>
                    <th class="width85"><span>{{item.match_type}}</span></th>
                    <th class="width250"><span>{{item.start_time}}-{{item.end_time}}</span></th>
                    <th class="width85"><span>{{item.round_time}}</span></th>
                    <th class="width110"><span>{{item.map}}</span></th>
                    <th class="width70"><span>{{item.score1}}:{{item.score2}}</span></th>
                    <th class="width50"><span>{{item.kill}}</span></th>
                    <th class="width50"><span>{{item.is_win}}</span></th>
                    <th class="width70"><span>{{item.rws}}</span></th>
                    <th class="width70"><span>{{item.rating}}</span></th>
                    <th class="width110"><a @click="todemo(item.match_code)">查看详情</a></th>
                </tr>
            </tbody>    
        </table>
        <div class="block">
            <el-pagination
                v-if="demolist.length>=15"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="pagesize"
                layout="total, prev, pager, next, jumper"
                :total="demolist.length">
            </el-pagination>
        </div>
    </div>
</template>

<script>
var qs = require('qs');
export default {
  data() {
      return {
          demolist: [],
          currentPage: 1,
          pagesize: 15,
          demodata: {}
      }
  },
  methods: {
    handleCurrentChange: function(currentPage){
        this.currentPage = currentPage;
    },
    todemo(code){
        let matchcode = {match_code: code};
        this.$axios.post("/data/demo/",qs.stringify(matchcode),{
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            }
        }).then(response=>{
            sessionStorage.setItem("matchdata", JSON.stringify(response.data));
            this.$router.push({name:'data',query:{match:code}})
        })
    }
  },
  created(){
    this.demolist = JSON.parse(sessionStorage.getItem("demodict"))['0']
  }
}
</script>

<style scoped>
.tablematch{
    font-size: 9px;
    text-align: center;
}
#tableheader{
    color: #b6d2ea;
}
.table{
    color:palevioletred;
}
.width50{
    width: 50px;
    height: 30px;
}
.width70{
    width: 70px;
    height: 30px;
}
.width85{
    width: 85px;
    height: 30px;
}
.width110{
    width: 110px;
    height: 30px;
}
.width250{
    width: 250px;
    height: 30px;
}
</style>