<template>
  <div id="app">
    <m-header></m-header>
    <div class="mybody">
      <el-input class="inputid"
        placeholder="请输入玩家id"
        v-model="input" >
      </el-input>
      <el-button @click="search" @keyup.enter="search" class="searchlogo" icon="el-icon-search" circle></el-button>
      <div class="maintitle">
        <h3 class="title">玩家数据面板</h3>
      </div>
      <div class="dataimg"></div>
      <div class="playerdata">
        <router-view v-if="showsearch"></router-view>
      </div>
    </div>
    <m-footer></m-footer>
  </div>
</template>

<script>
import MHeader from './components/Header'
import MFooter from './components/Footer'

export default { 
  data() {
    return {
      input: '',
      showsearch: true
    }
  },
  methods: {
    search(){
      if(this.input!=""){
        //等于当前路由才跳转，不然会报错
        if(this.$route.query.keyword!=this.input){
          this.$router.push({name:'search',query:{keyword:this.input}})
        }
        this.showsearch = false;
        // $nextTick 是在下次 DOM 更新循环结束之后执行延迟回调
        //在修改数据之后使用 $nextTick，则可以在回调中获取更新后的 DOM
        this.$nextTick(() => (this.showsearch = true))
      }
    }
  },
  components:{
    MHeader,
    MFooter
  }
}
</script>

